<template>
  <article
    class="comments-modal-mobile mobile mobile-full-page-modal flex column"
    @click.stop
  >
    <div class="modal-header big-box-item content-box flex space-between">
      <a class="center-childs" @click="closeModal">
        <img
          src="https://img.icons8.com/fluent-systems-filled/18/000000/x.png"
        />
      </a>
      <p class="title">Comments</p>
      <button class="primary-publish">
        Directs
      </button>
    </div>
    <div class="content-area flex column">
      <description class="big-box-item content-box" :post="myPost" />
      <comments class="big-box-item mobile fg-1" :comments="myPost.comments" />
      <add-comment class="big-box-item content-box" :postId="myPost._id" />
    </div>
  </article>
</template>

<script>
import description from '../post-view/post-modal-desc.cmp'
import comments from '../post-view/comments.cmp'
import addComment from '../post-view/add-comment.cmp'
export default {
  methods: {
    closeModal() {
      this.$store.commit('closeCommentsModal')
    },
  },
  computed: {
    myPost() {
      const postId = this.$store.getters.commentsModalPostId
      const { feedPosts } = this.$store.getters
      const post = feedPosts.find((currPost) => currPost._id === postId)
      return post
    },
  },
  components: {
    description,
    comments,
    addComment,
  },
}
</script>

