<template>
  <div id="app">
    <app-header v-if="$store.getters.loggedInUser && !isMobileView" />
    <add-post-shadow v-if="$store.getters.isAddPostOpen && !isMobileView" />
    <post-shadow-screen v-if="$store.getters.postModalId" />

    <!-- mobile: -->
    <mobile-header v-if="$store.getters.loggedInUser && isMobileView" />
    <transition name="slide-vertically">
      <mobile-add-post v-if="$store.getters.isAddPostOpen && isMobileView" />
    </transition>
    <transition name="slide-vertically">
      <mobile-comments-modal
        v-if="$store.getters.isCommentsModalOpen && isMobileView"
      />
    </transition>
    <mobile-nav-bar v-if="$store.getters.loggedInUser && isMobileView" />

    <main class="main-container page-content">
      <router-view />
    </main>
  </div>
</template>

<script>
import './assets/css/main.css'
import { socketService } from './services/socket.service'
import { userService } from './services/user.service'
import postShadowScreen from './components/modals/post-shadow-screen.cmp'
import appHeader from './components/header/header.cmp'
import addPostShadow from './components/modals/add-post-shadow-screen.cmp'
import mobileHeader from './components/mobile/header.cmp'
import mobileNavBar from './components/mobile/nav-bar.cmp'
import mobileAddPost from './components/mobile/add-post.cmp'
import mobileCommentsModal from './components/mobile/comments.cmp'

export default {
  computed: {
    loggedInUser() {
      console.log('logeed in user: ', this.$store.getters.loggedInUser)
      return this.$store.getters.loggedInUser
    },
    isMobileView() {
      return this.$store.getters.isMobile
    },
  },
  components: {
    postShadowScreen,
    appHeader,
    addPostShadow,
    mobileNavBar,
    mobileHeader,
    mobileAddPost,
    mobileCommentsModal,
  },
  created() {
    window.addEventListener('resize', () => {
      console.log('onResize')
      this.$store.commit('updateScreenWidth')
    })
  },
  watch: {
    loggedInUser: {
      immediate: true,
      handler(newVal, oldVal) {
        if (newVal) {
          if (oldVal) socketService.terminate()
          socketService.setup()

          //real time post manipulation updating:
          socketService.join(this.loggedInUser._id)
          socketService.on('postExternalManipulation', (data) => {
            this.$store.dispatch({ type: 'postExternalManipulation', data })
          })

          //real time activities updating:
          this.$store.dispatch('loadActivities')
          socketService.on('addExternalActivity', (data) =>
            this.$store.commit({ type: 'addExternalActivity', activity: data })
          )
          socketService.on('removeExternalActivity', (data) =>
            this.$store.commit({
              type: 'removeExternalActivity',
              activity: data,
            })
          )
        } else {
          if (oldVal) socketService.terminate()
        }
      },
    },
  },
}
</script>
