<template>
  <a class="view-all-comments" @click="loadPostModal"
    >View all {{ commentsQuantity }} comments</a
  >
</template>

<script>
export default {
  props: ['commentsQuantity', 'postId'],
  methods: {
    loadPostModal() {
      if (this.$store.getters.isMobile) {
        this.$store.dispatch('closeAllMobileModals')
        this.$store.commit({
          type: 'openCommentsModal',
          postId: this.postId,
        })
      } else {
        // this.$store.dispatch({ type: 'loadPostModal', postId: this.postId })
        this.$store.commit({ type: 'updatePostModalId', postId: this.postId })
      }
    },
  },
}
</script>

<style></style>
