<template>
  <div class="add-a-comment-box flex space-between">
    <div class="flex align-center">
      <svg
        aria-label="Emoji"
        class="_8-yf5 "
        fill="#262626"
        height="24"
        viewBox="0 0 48 48"
        width="24"
      >
        <path
          d="M24 48C10.8 48 0 37.2 0 24S10.8 0 24 0s24 10.8 24 24-10.8 24-24 24zm0-45C12.4 3 3 12.4 3 24s9.4 21 21 21 21-9.4 21-21S35.6 3 24 3z"
        ></path>
        <path
          d="M34.9 24c0-1.4-1.1-2.5-2.5-2.5s-2.5 1.1-2.5 2.5 1.1 2.5 2.5 2.5 2.5-1.1 2.5-2.5zm-21.8 0c0-1.4 1.1-2.5 2.5-2.5s2.5 1.1 2.5 2.5-1.1 2.5-2.5 2.5-2.5-1.1-2.5-2.5zM24 37.3c-5.2 0-8-3.5-8.2-3.7-.5-.6-.4-1.6.2-2.1.6-.5 1.6-.4 2.1.2.1.1 2.1 2.5 5.8 2.5 3.7 0 5.8-2.5 5.8-2.5.5-.6 1.5-.7 2.1-.2.6.5.7 1.5.2 2.1 0 .2-2.8 3.7-8 3.7z"
        ></path>
      </svg>
      <form hidden :id="formId" @submit.prevent="addComment"></form>
      <input
        type="text"
        placeholder="Add a comment..."
        v-model="comment.txt"
        :form="formId"
      />
    </div>
    <button
      type="submit"
      :form="formId"
      class="primary-publish"
      :class="activnessClass"
    >
      Post
    </button>
  </div>
</template>

<script>
import utilService from '../../services/util.service'
export default {
  props: ['postId'],
  data() {
    return {
      comment: {
        txt: '',
      },
      formId: utilService.makeId(),
    }
  },
  methods: {
    addComment() {
      if (!this.comment.txt) return
      const deepComment = JSON.parse(JSON.stringify(this.comment))
      this.$store.dispatch({
        type: 'addComment',
        comment: deepComment,
        postId: this.postId,
      })
      this.comment.txt = ''
    },
  },
  computed: {
    activnessClass() {
      return {
        'not-active': !this.comment.txt,
      }
    },
  },
}
</script>

<style></style>
