<template>
  <article
    class="add-post mobile mobile-full-page-modal flex column"
    @click.stop
  >
    <input
      id="uploadImgAddPost"
      hidden
      ref="imgInput"
      @input="passImgToCropper"
      type="file"
    />
    <div class="modal-header big-box-item content-box flex space-between">
      <a class="center-childs" @click="$store.commit('closeAddPost')">
        <img
          src="https://img.icons8.com/fluent-systems-filled/18/000000/x.png"
        />
      </a>
      <p class="title">New Post</p>
      <button class="primary-publish" :class="activnessClass" @click="addPost">
        Post
      </button>
    </div>
    <div class="content-area">
      <div class="img-area square flex justify-center">
        <div class="square-child flex justify-center">
          <img
            class="post-img-mobile"
            v-if="post.imgUri && !isCropperOpen"
            :src="post.imgUri"
          />
          <label
            class="post-img-placeholder-mobile center-childs pointer"
            v-if="!post.imgUri && !isCropperOpen"
            for="uploadImgAddPost"
          >
            <img src="@/assets/img/icons/insert-picture.svg" alt="C" />
          </label>
          <cropper
            v-if="isCropperOpen"
            class="mobile"
            :imgSrc="srcToCropper"
            @gotCroppedImg="changePicture"
          />
        </div>
      </div>
      <div class="big-box fg-1">
        <form
          id="addPostForm"
          hidden
          @submit.prevent="addPost"
          class="add-post-form"
        ></form>
        <div class="big-box-item inputs-box fg-1 flex column">
          <div class="content-box flex">
            <input
              class="classic-input fg-1"
              type="text"
              placeholder="caption"
              v-model="post.txt"
            />
          </div>
          <div class="content-box flex">
            <input
              class="classic-input fg-1"
              type="text"
              placeholder="location"
              v-model="post.loc.name"
            />
          </div>
        </div>
      </div>
    </div>
  </article>
</template>

<script>
import cropper from '../modals/cropper'
import postHeader from '../post-view/post-header.cmp'
export default {
  data() {
    return {
      isCropperOpen: false,
      srcToImg: null,
      srcToCropper: null,
      post: {
        txt: '',
        loc: {
          name: '',
        },
        imgUri: null,
      },
    }
  },
  methods: {
    changePicture(imgUrl) {
      this.isCropperOpen = false
      this.post.imgUri = imgUrl
    },
    passImgToCropper(ev) {
      const input = this.$refs.imgInput
      if (input.files && input.files[0]) {
        let reader = new FileReader()
        reader.onload = (e) => {
          this.srcToCropper = e.target.result
          this.isCropperOpen = true
        }
        reader.readAsDataURL(input.files[0])
      }
    },
    addPost() {
      if (!this.post.txt || !this.post.imgUri) {
        return
      }
      const deepPost = JSON.parse(JSON.stringify(this.post))
      this.$store.dispatch({ type: 'addPost', postDetails: deepPost })
      this.$store.commit('closeAddPost')
    },
  },
  computed: {
    demoPostData() {
      const loggedInUser = this.$store.getters.loggedInUser
      return {
        by: {
          username: loggedInUser.username,
          profileImgUrl: loggedInUser.profileImgUrl,
        },
        loc: this.post.loc,
      }
    },
    activnessClass() {
      return {
        'not-active': !this.post.txt || !this.post.imgUri,
      }
    },
  },
  components: {
    cropper,
    postHeader,
  },
}
</script>

