import Vue from 'vue'
import Vuex from 'vuex'
import postStore from './post.store'
import userStore from './user.store'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    postModalId: null,
    isAddPostOpen: false,
    isActivitiesOpen: false,
    currCommentsModalPostId: null,
    screenWidth: Math.max(
      document.body.scrollWidth,
      document.documentElement.scrollWidth,
      document.body.offsetWidth,
      document.documentElement.offsetWidth,
      document.documentElement.clientWidth
    ),
  },
  mutations: {
    updatePostModalId(state, { postId }) {
      state.postModalId = postId
    },
    closePostModal(state) {
      state.postModalId = null
    },
    updateScreenWidth(state) {
      console.log('updating screen width')
      state.screenWidth = Math.max(
        document.body.scrollWidth,
        document.documentElement.scrollWidth,
        document.body.offsetWidth,
        document.documentElement.offsetWidth,
        document.documentElement.clientWidth
      )
    },
    openAddPost(state) {
      state.isAddPostOpen = true
    },
    closeAddPost(state) {
      state.isAddPostOpen = false
    },
    openCommentsModal(state, { postId }) {
      state.currCommentsModalPostId = postId
    },
    closeCommentsModal(state) {
      state.currCommentsModalPostId = null
    },
    closeMobileFullModals(state) {
      state.isAddPostOpen = false
      state.currCommentsModalPostId = false
    },
  },
  actions: {
    closeAllMobileModals(context) {
      context.commit('closeMobileFullModals')
      // context.commit('closeMobileGeneralModals')
    },
  },
  getters: {
    isMobile(state) {
      return state.screenWidth <= 620
    },
    postModalId(state) {
      return state.postModalId
    },
    isAddPostOpen(state) {
      return state.isAddPostOpen
    },
    isCommentsModalOpen(state) {
      return state.currCommentsModalPostId
    },
    commentsModalPostId(state) {
      return state.currCommentsModalPostId
    },
    isActivitiesOpen(state) {
      return state.isActivitiesOpen
    },
  },
  modules: {
    postStore,
    userStore,
  },
})
